.student-homepage-container {
  width: 100%;
}

.student-homepage-container .student-home-banner {
  margin-top: 8vh;
  padding: 1rem 2.5rem;
  margin-bottom: 1rem;
  width: 100%;
  height: 430px;
  display: flex;
}

.student-homepage-container .student-home-banner .banner-content {
  width: 35%;
  height: 100%;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}

.student-homepage-container .student-home-banner .banner-content h1 {
  color: #00b255;
  font-size: 4em;
  margin-bottom: 2rem;
  text-shadow: 0px 2px 3px rgba(150, 150, 150, 1);
}

.student-homepage-container .student-home-banner .banner-content h3 {
  color: #00b255;
  font-size: 3rem;
  margin: 0;
  text-shadow: 0px 2px 3px rgba(150, 150, 150, 0.5);
}

.student-homepage-container .student-home-banner .banner-content p {
  margin-bottom: 2rem;
}

.student-homepage-container .student-home-banner .banner-content img {
  position: absolute;
  top: -30%;
  left: 40%;
  /* z-index: -1; */
}

.student-homepage-container .student-home-banner .banner-content button {
  width: 30%;
  padding: 0.5rem 1rem;
  border: none;
  outline: none;
  background-color: #3d4051;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border-radius: 5px;
}

.student-homepage-container .student-home-banner .banner-image {
  width: 65%;
  height: 100%;
  background-image: url("./assets/bg4.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.student-homepage-container .personal-learning-container {
  padding-top: 10rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-image: url("./assets/bg2.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header {
  width: 95%;
  position: absolute;
  top: 5%;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text {
  width: 30%;
  display: flex;
  flex-direction: column;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text
  p {
  font-size: 2.5rem;
  font-weight: bold;
  color: #00b255;
  margin-bottom: 10px;
  text-shadow: 0px 2px 3px rgba(150, 150, 150, 0.5);
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text
  .text-dash {
  width: 80px;
  height: 5px;
  background-color: #00b255;
  border-radius: 10px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body {
  width: 100%;
  z-index: 3;
  padding-left: 5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards {
  width: 20vw;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  padding-top: 70px;
  margin: 0 2.5rem 3rem 0;
  /* display: flex; */
  flex-direction: column;
  position: relative;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-head {
  width: 85%;
  height: 50px;
  border-radius: 0 50px 50px 0;
  position: absolute;
  padding: 0 1rem;
  top: 5%;
  left: -2.5%;
  display: flex;
  align-items: center;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-head
  p {
  color: #fff;
  font-size: 18px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-foot {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  padding: 0 1rem;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 10px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item
  .chap-name {
  width: 70%;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item
  .skill {
  width: 30%;
  display: flex;
  justify-content: flex-end;
}

