.iigp-home-container {
	width: 100%;
}

.iigp-home-container .iigp-ads-banner {
	width: 100%;
	height: 100vh;
	background-image: url("./assets/banner.png");
	background-size: contain;
	background-repeat: no-repeat;
	background-position: right;
	position: relative;
	display: flex;
	align-items: center;
	padding: 0 3.5rem;
}

.iigp-home-container .iigp-ads-banner .ads-content {
	width: 40%;
	display: flex;
	flex-direction: column;
}

.iigp-home-container .iigp-ads-banner .ads-content h1 {
	color: #00b255;
	font-size: 2.5rem;
}

.iigp-home-container .iigp-ads-banner .ads-content h1:nth-child(2) {
	font-size: 4rem;
	font-weight: bold;
}

.iigp-home-container .iigp-ads-banner .ads-content button {
	margin: 2rem 0;
	border: none;
	outline: none;
	width: 25%;
	padding: 0.8rem 0;
	border-radius: 10px;
	background-color: #3d4051;
	color: #fff;
}

.iigp-home-container .membership-cont {
	display: flex;
	flex-direction: column;
	background-image: url("./assets/discover-blue-bg-480-2x.webp");
	justify-content: center;
	align-items: center;
	background-size: 100% 100%;
	padding: 2rem;
}

.iigp-home-container .membership-cont h1 {
	color: #fff;
	margin: 0;
	font-family: "Roboto Slab", sans-serif;
}

.iigp-home-container .membership-cont p {
	margin: 1.5rem;
	color: #fff;
	font-family: "Roboto Slab", sans-serif;
}

.iigp-home-container .membership-cont button {
	border-radius: 5px;
	color: #fff;
	padding: 5px 35px;
	background-color: #52b700;
	border: none;
}

.iigp-home-container .membership-cont .membership-box {
	display: flex;
	/* flex-direction: row; */
	justify-content: space-evenly;
}

.iigp-home-container .membership-cont .membership-box1 {
	display: flex;
	flex-direction: column;
	width: 25%;
	box-sizing: border-box;
	background-color: #fff;
	padding: 1rem;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
}

.iigp-home-container .membership-cont .membership-box1 h5 {
	color: orange;
	padding: 0.5rem 0;
	font-size: 24px;
	font-weight: bold;
}

.iigp-home-container .membership-cont .membership-box1:nth-child(2) h5 {
	color: #00aeef;
}
.iigp-home-container .membership-cont .membership-box1:nth-child(3) h5 {
	color: #7a67d7;
}

.iigp-home-container .membership-cont .membership-box1 p {
	padding: 0.5rem 10px;
	margin: 0;
	color: #545454;
	line-height: 21px;
}

.iigp-home-container .membership-cont .membership-box1 a,
.iigp-home-container .membership-cont .membership-box1 i {
	color: orange;
	padding: 0 4px;
	padding-bottom: 10px;
}
.iigp-home-container .membership-cont .membership-box1:nth-child(2) a {
	color: #00aeef;
}
.iigp-home-container .membership-cont .membership-box1:nth-child(2) i {
	color: #00aeef;
}
.iigp-home-container .membership-cont .membership-box1:nth-child(3) a {
	color: #7a67d7;
}
.iigp-home-container .membership-cont .membership-box1:nth-child(3) i {
	color: #7a67d7;
}
