.students-participation-body {
    width: 100%;
    height:  100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.students-participation-body .stu-list {
    width: 30%;
    max-height: 72vh;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.students-participation-body .stu-list .stu-label{
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 15px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.students-participation-body .stu-list .stu-label:hover {
    background-color: #f5f7fa;
}

.students-participation-body .stu-list .stu-label img {
    height: 50px;
    width: 50px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 10px;
}

.students-participation-body .stu-list .stu-label p {
    color: #DF5400;
}
.students-participation-body .participation-tbl {
    width: 70%;
    height: 100%;
}