*{
    margin: 0;
    padding: 0;
}
.container{
    width: 100%;
    margin-top: 3rem ;
    margin-bottom: 3rem;
    padding:1rem;

}

.container .content{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.container .content .footer1{
    width: 25%;

}

.container .content .footer1 img{
    width: 81px;
    height: 31px;
}

.container .content .footer1 p{
    width: 85%;
    margin-top: 10px;
    font-size: 13px;
}

.container .content .footer1 button{
    margin-top: 10px;
    border-radius: 5px;
    padding: 3px 30px;
    background-color: #52b700;
    color: #fff;
    border: none;
}

