.iigp-nav-container {
    width: 100%;
    height: 8vh;
    padding: 0 3.5rem;
    position: fixed;
    top: 0;
    z-index: 999;
    /* box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15); */
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
}

.iigp-nav-container .iigp-logo-wrapper {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.iigp-nav-container .iigp-logo-wrapper img {
    height: 50%;
}

.iigp-nav-container .iigp-menu-wrapper {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.iigp-nav-container .iigp-menu-wrapper .link-box {
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.iigp-nav-container .iigp-menu-wrapper .link-box::before {
    content: '';
    position: absolute;
    width: 0;
    bottom: -5px;
    height: 3px;
    background-color:#fff;
}

.iigp-nav-container .iigp-menu-wrapper .link-box:hover::before {
    content: '';
    position: absolute;
    width: 70px;
    bottom: 0px;
    height: 3px;
    background-color:#fff;
    border-radius: 3px;
    transition: width 0.5s ease-in-out;
    transform-origin: left;
}

.iigp-nav-container .iigp-menu-wrapper .link-box:last-child:hover::before {
    content: '';
    position: absolute;
    width: 0px;
    bottom: 0px;
    height: 3px;
    background-color:#fff;
    border-radius: 3px;
    transition: width 0.5s ease-in-out;
    transform-origin: left;
}

.iigp-nav-container .iigp-menu-wrapper .link-box p {
    font-size: 18px;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
}

.iigp-nav-container .iigp-menu-wrapper .link-box button {
    border: 2px solid #fff;
    background-color: transparent;
    padding: 0.2rem 2rem;
    color: #fff;
    font-size: 18px;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
}