.sidebar-cont .sidebar .creativity,
.sidebar-cont .sidebar .Entrepreneurship,
.sidebar-cont .sidebar .ProblemSolving,
.sidebar-cont .sidebar .Assignments,
.sidebar-cont .sidebar .Practices,
.sidebar-cont .sidebar .Attendance,
.sidebar-cont .sidebar .Behaviour {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
}
.sidebar-cont .sidebar .creativity:hover,
.sidebar-cont .sidebar .Entrepreneurship:hover,
.sidebar-cont .sidebar .ProblemSolving:hover,
.sidebar-cont .sidebar .Assignments:hover,
.sidebar-cont .sidebar .Practices:hover,
.sidebar-cont .sidebar .Attendance:hover,
.sidebar-cont .sidebar .Behaviour:hover {
  background-color: black;
  color: #f1f1f1;
}

.sidebar-cont .sidebar .creativity.active,
.sidebar-cont .sidebar .Entrepreneurship.active,
.sidebar-cont .sidebar .ProblemSolving.active,
.sidebar-cont .sidebar .Assignments.active,
.sidebar-cont .sidebar .Practices.active,
.sidebar-cont .sidebar .Attendance.active,
.sidebar-cont .sidebar .Behaviour.active {
  background-color: black;
  color: #f1f1f1;
}

.sidebar-cont .sidebar .creativity.active .icon-name p,
.sidebar-cont .sidebar .creativity.active .icon i,
.sidebar-cont .sidebar .Entrepreneurship.active .icon-name p,
.sidebar-cont .sidebar .Entrepreneurship.active .icon i,
.sidebar-cont .sidebar .ProblemSolving.active .icon-name p,
.sidebar-cont .sidebar .ProblemSolving.active .icon i,
.sidebar-cont .sidebar .Assignments.active .icon-name p,
.sidebar-cont .sidebar .Assignments.active .icon i,
.sidebar-cont .sidebar .Practices.active .icon-name p,
.sidebar-cont .sidebar .Practices.active .icon i,
.sidebar-cont .sidebar .Attendance.active .icon-name p,
.sidebar-cont .sidebar .Attendance.active .icon i,
.sidebar-cont .sidebar .Behaviour.active .icon-name p
.sidebar-cont .sidebar .Behaviour.active .icon i{
  color: #f1f1f1;
}

@media screen and (max-width: 700px) {
  .sidebar-cont .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar-cont .sidebar p {
    float: left;
  }
  .content {
    margin-left: 0;
  }
}

@media screen and (max-width: 400px) {
  .sidebar p {
    text-align: center;
    float: none;
  }
}
